  }

  .titleJR, .subTitle, .quote  {
    text-align: center;
    padding-right: 0px;
    padding-top: 0px;
  }

  .slide, .slideSectionLeft, .slideSectionRight {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding: 0px;

  }
  .projTitle {
    font-size: 8vw;
  }
  .projectImage {
    width: 50vw;
  }
  .slideSectionRight, .slideSectionLeft { 
    width: 70%;
    height: -20px;
